/* ==========================================================================
   =Posts Navigation(s)
   ========================================================================== */
 
/* =Post Pagetion
   ========================================================================== */

	.pagination {
		margin-bottom: var(--spacing-3);
	}
	
	/*
	 * 1. Clearfix hack 
	 */
	 
	.pagination:after {	/* 1 */
		visibility: hidden;
		display: block;
		font-size: 0;
		content: " ";
		clear: both;
		height: 0;
	}
	
	.pagination a {
		text-decoration: none;
	}	
	
	.pagination .nav-links {
		text-align: left;
	}
	
	.rtl .pagination .nav-links {
		text-align: right;
	}
	
	.pagination .page-numbers {
		display: inline-block;
		margin-right: var(--spacing-4);
		text-align: center;
	}
	
	.rtl .pagination .page-numbers {
		margin-right: 0;
		margin-left: var(--spacing-4);
		text-align: center;
	}
	
	.pagination .page-numbers:last-child {
		margin-right: 0;
	}	
	
	.rtl .pagination .page-numbers:last-child {
		margin-left: 0;
	}
	
	.pagination .page-numbers.current {
		position: relative;
		min-width: var(--spacing-4);
		min-height: var(--spacing-4);
		padding: var(--spacing-2) var(--spacing-3);
		border-radius: var(--border-radius-buttons);
		background-color: var(--background-color);
	}

	.page-numbers.prev,
	.page-numbers.next,
	.rovenblog-prev-next a {
		text-decoration: none;
	}
	
	.page-numbers.prev .screen-reader-text,
	.page-numbers.next .screen-reader-text {
		clip: auto;
		height: auto;
		overflow: auto;
		position: relative !important;
		width: auto;
		text-transform: uppercase;
		clip-path: inherit !important;
		margin: auto !important;
		top: auto !important;
	}
	
	body:not(.rtl) .page-numbers.prev span:before {
		content: "\e908";
		font-family: 'rovenblogiconfont';
		padding-right: var(--spacing-2);
	}
	
	.rtl .page-numbers.prev span:after {
		content: "\e90a";
		font-family: 'rovenblogiconfont';
		padding-left: var(--spacing-2);
	}
	
	body:not(.rtl) .page-numbers.next span:after {
		content: "\e90a";
		font-family: 'rovenblogiconfont';
		padding-left: var(--spacing-2);
	}

	.rtl .page-numbers.next span:before {
		content: "\e908";
		font-family: 'rovenblogiconfont';
		padding-right: var(--spacing-2);
	}

	@media (max-width: 767px) {
		
		.pagination .page-numbers {
			margin-right: var(--spacing-3);
		}
		
		.rtl .pagination .page-numbers {
			margin-right: 0;
			margin-left: var(--spacing-3);
		}
	
		.pagination .page-numbers {
			display: none;
		}
		
		.page-numbers.current .screen-reader-text {
			clip: auto;
			height: auto;
			overflow: auto;
			position: relative !important;
			width: auto;
			clip-path: none;
			top: auto;
		}
	
		.pagination .page-numbers.prev,
		.pagination .page-numbers.next,
		.pagination .page-numbers.current {
			display: block;
			margin: var(--spacing-2) 0
		}

		.pagination .page-numbers.prev {
			text-align: left;
		}
		
		.rtl .pagination .page-numbers.prev {
			text-align: right;
		}
		
		.pagination .page-numbers.next {
			text-align: right;
		}
		
		.rtl .pagination .page-numbers.next {
			text-align: right;
		}
		
	}
	
	@media (min-width: 768px) {
		
		.pagination {
			margin-bottom: var(--spacing-4);
		}
		
	}
	
	.rovenblog-prev-next a:first-child {
		margin-right: var(--spacing-4);
	}

	.rovenblog-prev-next a {
		display: block;
		margin: var(--spacing-2) 0;
	}

	.rovenblog-prev-next a {
		text-align: right;
	}
	