/* =Share
   ========================================================================== */ 	
   
	.rb-social-share {
		list-style: none;
		margin-bottom: 0;
		text-align: center;
	}
	
	.rb-has-sidebar .rb-social-share {
		text-align: left;
	}
	
	.rtl.rb-has-sidebar .rb-social-share {
		text-align: right;
	}
	
	.rb-social-share li {
		display: inline-block;
		padding: var(--font-spacing-1);
	}
	
	.rb-social-share li small {
		font-size: 70%;
	}
	
	.rb-social-share li a {
		padding: var(--font-spacing-2) var(--font-spacing-2) calc( var(--font-spacing-2) - 2px );
		border-radius: var(--border-radius-buttons);
		display: block;
		line-height: 1;
		color: #fff;
		min-width: 20px;
		text-decoration: none;
	}
	
	.rb-social-share li a:hover {
		color: #fff;
	}
	
/* =Share Floating
   ========================================================================== */ 

	#rb-social-share-floating {
		display: none;
	}	

	@media (min-width: 1200px) {
		
		#rb-social-share-floating {
			display: block;
			position: absolute;
			top: 0;
			bottom: 0;
			left: calc( -1 * var(--spacing-8) );
			z-index: 1;
		}
		
		.rtl #rb-social-share-floating {
			left: auto;
			right: calc( -1 * var(--spacing-8) );
		}
		
		#rb-social-share-floating .rb-social-share {
			list-style: none;	
			position: sticky;
			top: 0;
		}
		
		#rb-social-share-floating .rb-social-share {
			top: var(--sticky-offset);
		}
		
		#rb-social-share-floating .rb-social-share li {
			display: block;
		}
		
		.rb-has-sidebar #rb-social-share-floating {
			display: none;
		}	
		
	}	
	