/* ==========================================================================
   =Slider
   ========================================================================== */
   
	.rb-slider { 
		position: relative;
		margin-bottom: var(--spacing-3);
	}

	.rb-slider-content { 
		position: relative;
	}

	.rb-slider-slides {
		margin-bottom: 0;
		list-style: none;
	}
	
	.rb-slider-slides { 
		position: relative;
		margin: calc( -1 * var(--spacing-3) );
	}

	.rb-slider-slides li {
		padding: var(--spacing-3);
	}
	
	.rb-slider-slides li > *:last-child {
		margin-bottom: 0;
	}	

	.rb-slider-pager {}

	.rb-slider-pager .slick-dots {
		margin-bottom: 0;
		list-style: none;
		text-align: center;
	}

	.rb-slider-pager .slick-dots li {
		position: relative;
		z-index: 2;
		display: inline-block;
		padding: var(--font-spacing-1);
	}	

	.rb-slider-pager .slick-dots li.slick-active {}

	.rb-slider-pager .slick-dots li button{
		display: block;
		padding: 0;
		width: var(--font-spacing-2);
		height: var(--font-spacing-2);
		border: 0;
		border-radius: var(--border-radius-buttons);
		cursor: pointer;
		outline: none;
		color: transparent;
		background: var(--background-color);
		font-size: 0;
		line-height: 0;
	}

	.rb-slider-pager .slick-dots li:hover button,
	.rb-slider-pager .slick-dots li.slick-active button {
		background: var(--color-accent-1);
	}	
	
	@media (min-width: 768px) {
		
		.rb-slider { 
			margin-bottom: var(--spacing-4);
		}
		
	}
	
	@media (min-width: 992px) {
		
		.rb-slider-pager {
			margin-top: var(--spacing-3);
		}
		
		.rb-slider-pager .slick-dots li button {
			width: calc( var(--font-spacing-2) + var(--font-spacing-1) ) ;
			height: calc( var(--font-spacing-2) + var(--font-spacing-1) ) ;
		}
		
		.rb-slider .rb-slider-arrows a {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			font-size: var(--font-size-6); 
			border: 2px solid #ffffff;
			background-color: var(--color-accent-1);
			border-radius: var(--border-radius-buttons);
			color: #ffffff;
			line-height: var(--font-spacing-4);
			width: var(--font-spacing-4);
			height: var(--font-spacing-4);
			text-align: center;
			text-decoration: none;
		}
		
		.rb-dark-mode .rb-slider .rb-slider-arrows a {
			border-color: var(--background-color-body);
			text-decoration: none;
		}

		.rb-slider .rb-slider-arrows a:hover { 
			background-color: var(--color-accent-2);
		}
		
		.rb-slider .rb-slider-arrows .slick-prev {
			left: calc( -1 * var(--spacing-3) );
		}

		.rb-slider .rb-slider-arrows .slick-next {
			right: calc( -1 * var(--spacing-3) );
		}
		
	}
	
	@media (min-width: 1200px) {
		
		.rb-slider .rb-slider-arrows a {
			font-size: var(--font-size-6); 
			line-height: var(--font-spacing-5);
			width: var(--font-spacing-5);
			height: var(--font-spacing-5);

		}
		
		.rb-slider .rb-slider-arrows .slick-prev {
			left: calc( -1 * var(--spacing-4) );
		}
		
		.rb-slider .rb-slider-arrows .slick-next {
			right: calc( -1 * var(--spacing-4) );
		}
		
	}