/* ==========================================================================
   =Asymetric Grid
   ========================================================================== */
   
	.rb-asymetric-grid {
		margin-bottom: var(--spacing-3);
	}
	
	.rb-asymetric-grid-item:not(:last-child) {
		padding-bottom: var(--spacing-3);
	}
	
	.rb-asymetric-grid-item > *:last-child {
		margin-bottom: 0;
	}

	@media (min-width: 768px) {

		.rb-asymetric-grid {
			margin-bottom: var(--spacing-4);
		}
		
		.rb-asymetric-grid-item:not(:last-child) {
			padding-bottom: var(--spacing-4);
		}
	
		.rb-asymetric-grid.cols-2 {
			display: flex;
			gap: var(--spacing-4);	
			justify-content: space-between;
			flex-flow: row wrap;
		}

		.rb-asymetric-grid.cols-2 .rb-asymetric-grid-item {
			flex-basis: calc( 33.3333% - var(--spacing-3) );
			padding-bottom: 0;
		}
		
		.rb-asymetric-grid.cols-2 .rb-asymetric-grid-item:nth-child(2n+1) {
			flex-basis: calc( 66.6666% - var(--spacing-3) );
		}

	}
