/* ==========================================================================
   =Wrap
   ========================================================================== */
   
	/*
	 * 1. Fix sticky menu issue
	 */
   
	#rb-wrap {
		min-height: 759px; /* 1 */
	}
	
	@media( min-width: 1400px ) {
		
		#rb-wrap {
			min-height: 1080px; /* 1 */
		}
		
	}
	
/* ==========================================================================
   =Cta
   ========================================================================== */
   
    #rb-cta {
		padding-left: var(--spacing-3);
		padding-right: var(--spacing-3);
		color: var(--color-cta);
		background-color: var(--background-color-cta);
		font-weight: var(--font-weight-cta);
		font-family: var(--font-family-cta);
	}
	
	#rb-cta-content {
		padding-top: var(--spacing-2);
		padding-bottom: var(--spacing-2);
		position: relative;
		max-width: var(--max-width-content);
		margin: 0 auto;
	}
	
	#rb-cta.rb-stretch-fullwidth #rb-cta-content {
		max-width: 100%;
	}
	
	#rb-cta.rb-border-bottom #rb-cta-content {
		border-bottom: 1px solid var(--border-color-cta);
	}
	
	#rb-cta.rb-border-bottom.rb-border-bottom-stretch-fullwidth {
		border-bottom: 1px solid var(--border-color-cta);
	}
	
	#rb-cta.rb-border-bottom.rb-border-bottom-stretch-fullwidth #rb-cta-content {
		border-bottom: 0 !important;
	}
	
	/*
	 * 1. Clearfix hack 
	 */
	#rb-cta-content:after {	/* 1 */
		visibility: hidden;
		display: block;
		font-size: 0;
		content: " ";
		clear: both;
		height: 0;
	}

	#rb-cta-content > *:last-child { margin-bottom: 0; }
	
	#rb-cta a {
		background-color: var(--background-color-cta-button); 
		color: var(--color-cta-button);
		padding: var(--spacing-1) var(--spacing-2);
		margin-left: var(--spacing-2);
		display: inline-block;
		border-radius: var(--border-radius-buttons);
		line-height: 1;
		text-decoration: none;
	}
	
	.rtl #rb-cta a {
		margin-left: 0;
		margin-right: var(--spacing-2);
	}
	
	#rb-cta a:focus,
	#rb-cta a:hover,
	#rb-cta a:active {
		background-color: var(--background-color-cta-button); 
		color: var(--color-cta-button);
	}

	@media( min-width: 768px ) {
		
		#rb-cta {
			padding-left: var(--spacing-4);
			padding-right: var(--spacing-4);
		}
		
	}

	@media( min-width: 992px ) {
		
		#rb-cta {
			padding-left: var(--spacing-5);
			padding-right: var(--spacing-5);
		}
		
	}

	@media( min-width: 1400px ) {
		
		#rb-cta {
			padding-left: var(--spacing-6);
			padding-right: var(--spacing-6);
		}
		
	}

	
/* ==========================================================================
   =Header Top
   ========================================================================== */
   
	#rb-header-top {
		padding-left: var(--spacing-3);
		padding-right: var(--spacing-3);
		color: var(--color-text);
		background-color: var(--background-color-header-top);
	}
	
	#rb-header-top-content {
		padding-top: var(--spacing-2);
		padding-bottom: var(--spacing-2);
		position: relative;
		max-width: var(--max-width-content);
