	}
	
	#rb-header-top.rb-stretch-fullwidth #rb-header-top-content {
		max-width: 100%;
	}
	
	#rb-header-top.rb-border-bottom #rb-header-top-content {
		border-bottom: 1px solid var(--border-color);
	}
	
	#rb-header-top.rb-border-bottom.rb-border-bottom-stretch-fullwidth {
		border-bottom: 1px solid var(--border-color);
	}
	
	#rb-header-top.rb-border-bottom.rb-border-bottom-stretch-fullwidth #rb-header-top-content {
		border-bottom: 0 !important;
	}
	
	/*
	 * 1. Clearfix hack 
	 */
	#rb-header-top-content:after {	/* 1 */
		visibility: hidden;
		display: block;
		font-size: 0;
		content: " ";
		clear: both;
		height: 0;
	}

	#rb-header-top-content > *:last-child { margin-bottom: 0; }
	
