		#rb-header-top {
			padding-left: var(--spacing-4);
			padding-right: var(--spacing-4);
		}
		
	}

	@media( min-width: 992px ) {
		
		#rb-header-top {
			padding-left: var(--spacing-5);
			padding-right: var(--spacing-5);
		}
		
	}

	@media( min-width: 1400px ) {
		
		#rb-header-top {
			padding-left: var(--spacing-6);
			padding-right: var(--spacing-6);
		}
		
	}
   
/* ==========================================================================
   =Header
   ========================================================================== */
	
	#rb-header {
		padding-top: var(--spacing-3);
		padding-bottom: var(--spacing-3);
		padding-left: var(--spacing-3);
		padding-right: var(--spacing-3);
		background-color: var(--background-color-header);
	}

	#rb-header-content {
		position: relative;
		max-width: var(--max-width-content);
		margin: 0 auto;
	}

	#rb-header.rb-stretch-fullwidth #rb-header-content {
		max-width: 100%;
	}

	/*
	 * 1. Clearfix hack 
	 */
	#rb-header-content:after {	/* 1 */
		visibility: hidden;
		display: block;
		font-size: 0;
		content: " ";
		clear: both;
		height: 0;
	}

	#rb-header-content > *:last-child { margin-bottom: 0; }

	.rb-sticky-header-desktop #rb-header.rb-header-sticky,
	.rb-sticky-header-mobile #rb-header.rb-header-sticky {
		padding-top: var(--spacing-3);
		padding-bottom: var(--spacing-3);
		background-color: var(--background-color-header);
		box-shadow: var(--box-shadow-header);
	}		
	
	.rb-sticky-header-mobile #rb-header {
		position: sticky;
		top: 0px;
		z-index: var(--z-index-header-sticky);
	}

	.admin-bar.rb-sticky-header-mobile #rb-header {
		top: 0;
	}

	.rb-header-navbar {
		display: flex;
		align-items: center;
		position: relative;
		width: 100%;
		justify-content: space-between;
	}
	
	.rb-header-navbar-col {
		display: flex;
		align-items: center;
		height: 100%;
		flex: 1;
	}	
	
	.rb-header-navbar-col:nth-child(1) {
		justify-content: flex-start;
	}
	
	.rb-header-navbar-col:nth-child(2) {
		justify-content: center;
	}
	
	.rb-header-navbar-col:nth-child(3) {
		justify-content: flex-end;
	}
	
	.rb-logo-top {}
	
	.rb-logo-top .rb-header-navbar-col:nth-child(2) { flex-grow: 5;}
	
	.rb-logo-left {}

	.rb-logo-left .rb-header-navbar-col:nth-child(1) { flex-grow: 5;}

	.rb-logo-center {}
	
	@media (max-width: 767.98px){
		
		.rb-logo-left .rb-header-navbar-col:nth-child(2) { display: none; }
	
	}
	
	@media (min-width: 1200px){
	
		.rb-logo-center .rb-header-navbar-col:nth-child(1),
		.rb-logo-center .rb-header-navbar-col:nth-child(3) { flex-grow: 2;}

		.rb-logo-left .rb-header-navbar-col:nth-child(1) { flex-grow: 1;}
		.rb-logo-left .rb-header-navbar-col:nth-child(2) { flex-grow: 5;}

	}
	
	@media (max-width: 1199.98px){
		
		.rb-logo-center .rb-header-navbar-col:nth-child(2) { flex-grow: 4;}
		
	}
	
	@media (min-width: 768px){
		
		#rb-header {
			padding-top: var(--spacing-4);
			padding-left: var(--spacing-4);
			padding-right: var(--spacing-4);
			padding-bottom:  var(--spacing-4);
		}
		
		.rb-sticky-header-mobile #rb-header {
			position: relative !important;
			top: auto;
		}
		
		.rb-sticky-header-desktop #rb-header {
			position: sticky !important;
			top: 0;
			z-index: var(--z-index-header-sticky);
		}
		
		.admin-bar.rb-sticky-header-desktop #rb-header {
			top: 32px;
		}
		
		
	}	
	
	@media( min-width: 992px ) {
	
		#rb-header {
			padding-left: var(--spacing-5);
			padding-right: var(--spacing-5);
		}
		
	}
	
	@media( min-width: 1200px ) {
	
		#rb-header {
			padding-top: var(--spacing-5);
			padding-bottom:  var(--spacing-5);
		}
		
	}

	@media( min-width: 1400px ) {
		
		#rb-header {
			padding-left: var(--spacing-6);
			padding-right: var(--spacing-6);
		}
		
	}
	
/* =Logo
   ========================================================================== */	
	
	.rb-logo {
		padding: 0 var(--spacing-2);
		font-size: var(--font-size-logo-mobile);
		color: var(--color-logo);
		font-family: var(--font-family-logo);
		font-weight: var(--font-weight-logo);
		line-height: 1;
		letter-spacing: var(--letter-spacing-logo);
		text-align: center;
		display: block;
		text-decoration: none;
	}
	
	@media( min-width: 1200px ) {
		
		.rb-logo-top .rb-logo {
			padding-bottom: var(--logo-padding-bottom);
		}
		
		.rb-logo-top.rb-header-sticky .rb-logo {
			padding-bottom: 0;
		}
	
	}
	
	.rb-logo-left .rb-logo {
		text-align: left;
	}
	
	.rb-header-sticky .rb-logo {
		font-size: var(--font-size-logo-mobile-sticky);
	}
	
	.rb-logo:focus,
	.rb-logo:hover,
	.rb-logo:active,
	.rb-logo:visited {
		color: var(--color-logo);
	}
	
	.rb-logo img {
		max-height: var(--max-height-logo-mobile);
		display: block;
	}
	
	.rb-header-sticky .rb-logo img {
		max-height: var(--max-height-logo-mobile-sticky);
	}
	
	.rb-logo img.rb-logo-image-dark-mode { display: none; }
	
	.rb-dark-mode .rb-logo img.rb-logo-image-dark-mode { display: block; }
	
	.rb-dark-mode .rb-logo img.rb-logo-image-light-mode { display: none; }
	
	@media(min-width: 768px) {
		
		.rb-logo {
			font-size: var(--font-size-logo-tablet);
		}	
		
		.rb-header-sticky .rb-logo {
			font-size: var(--font-size-logo-tablet-sticky);
		}
		
		.rb-logo img {
			max-height: var(--max-height-logo-tablet);
		}
		
		.rb-header-sticky .rb-logo img {
			max-height: var(--max-height-logo-tablet-sticky);
		}
		
	}
	
	@media(min-width: 1200px) {
		
		.rb-logo {
			font-size: var(--font-size-logo-desktop);
		}	

		.rb-header-sticky .rb-logo {
			font-size: var(--font-size-logo-desktop-sticky);
		}
		
		.rb-logo img {
			max-height: var(--max-height-logo-desktop);
		}
		
		.rb-header-sticky .rb-logo img {
			max-height: var(--max-height-logo-desktop-sticky);
		}
		
	}
	
/* =Social
   ========================================================================== */	
	
	.rb-social-header {
		list-style: none;
