/* =Menu
   ========================================================================== */	
	
	.rb-navmenu-notice {
		background-color: var(--background-color);
		border: 1px solid var(--border-color);
		padding: var(--spacing-2);
		border-radius: var(--border-radius-default);
		font-size: var(--font-size-1);
	}	
	
	.rb-navmenu-notice > * {
		margin: 0;
	}
	
	#rb-header .menu {}
	
	#rb-header .menu > ul {
		margin: 0;
		list-style: none;
		display: flex;
		align-items: flex-start;
		position: relative;
		height: 100%;
		justify-content: center;
		flex-wrap: wrap;
	}
	
	#rb-header .menu > ul > li {
		position: relative;
	}
	
	#rb-header .menu > ul > li.menu-item-has-children > a:after {
		content: "\e916";
		font-family: 'rovenblogiconfont';
		font-size: calc( var(--font-size-menu) - 2px );
		margin-left: 2px;
	}
	
	#rb-header .menu ul.sub-menu {
		text-align: left;
		margin: 0;
		list-style: none;
		position: relative;
		display: block;
		background-color: var(--background-color-submenu);
		border: 1px solid var(--border-color-submenu);
		box-shadow: var(--box-shadow-submenu);
	}
	
	#rb-header .menu ul.sub-menu {
		position: absolute;
		z-index: 1025;
		top: 100%;
		left: calc( -1 * var(--spacing-1) );
		display: none;
		min-width: 150px;
	}
	
	#rb-header .menu ul.sub-menu ul.sub-menu {
		top: -1px;
		left: 100%;
		display: none;
		position: absolute;
	}
	
	#rb-header .menu-item-has-children:focus-within > ul,
	#rb-header .menu-item-has-children:hover > ul { 
		display: block !important; 
	}
	
	#rb-header .menu ul a {
		display: block;
		height: 100%;
		white-space: nowrap;
		padding: var(--spacing-1) var(--spacing-menu);
		text-transform: var(--text-transform-submenu);
		font-size: var(--font-size-submenu);
		line-height: var(--line-height-submenu);
		font-family: var(--font-family-submenu);
		font-weight: var(--font-weight-submenu);
		color: var(--color-submenu);
		text-decoration: none;
	}
	
	#rb-header .menu ul a:focus,
	#rb-header .menu ul a:hover {
		color: var(--color-submenu-hover);
	}
	
	#rb-header .menu > ul > li > a {
		padding: 0 var(--spacing-menu);
		text-transform: var(--text-transform-menu);
		font-size: var(--font-size-menu);
		line-height: var(--line-height-menu);
		font-family: var(--font-family-menu);
		font-weight: var(--font-weight-menu);
		color: var(--color-menu);
	}
	
	#rb-header.rb-header-sticky .menu > ul > li > a {
		line-height: var(--line-height-menu-sticky);
	}
	
	#rb-header .menu > ul > li.current-menu-ancestor > a,
	#rb-header .menu > ul li.current-menu-item > a,
	#rb-header .menu > ul > li > a:focus,
	#rb-header .menu > ul > li > a:hover {
		color: var(--color-menu-hover);
	}

