	}	
	
	#rb-mobile-menu .menu-item-has-children > a {
		position: relative;
		padding-right: var(--spacing-4);
	}
	
	.rtl #rb-mobile-menu .menu-item-has-children > a {
		padding-left: var(--spacing-4);
		padding-right: 0;
	}
	
	#rb-mobile-menu .menu-item-has-children > a span {
		position: absolute;
		cursor: pointer;
		top: 50%;
		right: 0;
		transform: translateY(-50%);
	}
   
	.rtl #rb-mobile-menu .menu-item-has-children > a span {
		right: auto;
		left: 0;
	}
   
	.admin-bar  #rb-mobile-menu-wrap {
		padding-top: 32px;
	}

	
/* ==========================================================================
   =Back to top
   ========================================================================== */   
   
    #rb-back-to-top {
		position: fixed;
		z-index: var(--z-index-back-to-top);
		right: calc( -1 * var(--spacing-5) );
		bottom: var(--spacing-3);
		width: var(--spacing-4);
		height: var(--spacing-4);
		border-radius: var(--border-radius-buttons);
		background-color: var(--color-accent-1);
		color: #ffffff;
		line-height: var(--spacing-4);
		text-align: center;					
		cursor: pointer;
		transition: all 0.4s ease 0s;
		text-decoration: none;
	}
	
	.rtl #rb-back-to-top {
		right: auto;
		left: calc( -1 * var(--spacing-5) );
	}
	
	#rb-back-to-top i { transition: all 0.4s ease 0s; }
	
	#rb-back-to-top:focus,
	#rb-back-to-top:hover { background-color: var(--color-accent-2); }
	
	#rb-back-to-top:focus i,	
	#rb-back-to-top:hover i { color: #ffffff; }
	
	#rb-back-to-top.visible { right: var(--spacing-3); }
	.rtl #rb-back-to-top.visible { right: auto; left: var(--spacing-3); }
	
	#rb-back-to-top.gone { right: calc( -1 * var(--spacing-5) ); }	   
	.rtl #rb-back-to-top.gone { right: auto; left: calc( -1 * var(--spacing-5) ); }	
	
	body:not(.rb-has-back-to-top-mobile) #rb-back-to-top { display: none; }
	
	@media (min-width: 768px){
		
		#rb-back-to-top { display: block !important; }
		
		body:not(.rb-has-back-to-top-desktop) #rb-back-to-top { display: none !important; }
		
	}
   