/* ==========================================================================
   =Classic Grid
   ========================================================================== */
   
	.rb-grid {
		margin-bottom: var(--spacing-3);
	}
	
	.rb-grid-item:not(:last-child) {
		padding-bottom: var(--spacing-3);
	}
	
	#rb-header-top .rb-grid-item:not(:last-child) {
		padding-bottom: var(--spacing-2);
	}

	.rb-grid-item > *:last-child {
		margin-bottom: 0;
	}

	@media (min-width: 768px) {

		.rb-grid {
			margin-bottom: var(--spacing-4);
		}
		
		.rb-grid-item:not(:last-child) {
			padding-bottom: var(--spacing-4);
		}

		.rb-grid.cols-2,
		.rb-grid.cols-3,
		.rb-grid.cols-4 {
			display: flex;
			justify-content: flex-start;
			flex-flow: row wrap;
			gap: var(--spacing-4);
		}

		.rb-grid.cols-2 .rb-grid-item,
		.rb-grid.cols-3 .rb-grid-item,
		.rb-grid.cols-4 .rb-grid-item {
			flex-basis: calc( 49.9999% - var(--spacing-3) );
			padding-bottom: 0 !important;
		}
		
		.rb-grid.cols-3.mixed .rb-grid-item:nth-child(3n) {
			flex-basis: 100%;
		}
		
		.rb-grid.rb-grid-align-items-center {
			align-items: center;
		}
		
		.rb-grid.rb-grid-align-items-end {
			align-items: end;
		}

	}

	@media (min-width: 1200px) {
		
		.rb-grid.cols-3 .rb-grid-item,
		.rb-grid.cols-3.mixed .rb-grid-item:nth-child(3n)	{
			flex-basis: calc( 33.3333% - ( var(--spacing-6) / 3 ) );
		}
		
	}
	
	@media (min-width: 1400px) {
		
		.rb-grid.cols-4 .rb-grid-item {
			flex-basis: calc( 24.9999% - ( var(--spacing-8) / 4 ) );
		}
		
	}