/* ==========================================================================
   =Utilities
   ========================================================================== */
   
/* =Responsive embeds
   ========================================================================== */     
   
	.rb-media-wrapper { 
		position: relative; 
		padding-bottom: 56.25%; 
		height: 0; 
		overflow: hidden; 
		max-width: 100%; 
		margin-bottom: var(--font-spacing-4); 
	} 
	
	.wp-block-embed__wrapper .rb-media-wrapper {
		position: static;
		padding-bottom: 0;
	}

	
	.rb-media-wrapper iframe, 
	.rb-media-wrapper object, 
	.rb-media-wrapper embed { 
		position: absolute; 
		top: 0; 
		left: 0; 
		width: 100%; 
		height: 100%; 
	}
	
	.rtl .rb-media-wrapper iframe, 
	.rtl .rb-media-wrapper object, 
	.rtl .rb-media-wrapper embed { 
		left: auto;
		right: 0;
	}	

