/* =Align
   ========================================================================== */      
   
	.rb-text-align-left { text-align: left; }
	
	.rtl .rb-text-align-left { text-align: right; }
	
	.rb-text-align-center { text-align: center; }
   
	.rb-text-align-right { text-align: right; }
	
	.rtl .rb-text-align-right { text-align: left; }
	
	@media (max-width: 767px) {
	  
		.rb-text-align-left-mobile {
			text-align: left;
		}

		.rb-text-align-center-mobile {
			text-align: center;
		}

		.rb-text-align-right-mobile {
			text-align: right;
		}
	  
	}

	@media (min-width: 768px) and (max-width: 1199px) {
		
		.rb-text-align-left-tablet {
			text-align: left;
		}
		
		.rb-text-align-center-tablet {
			text-align: center;
		}
		
		.rb-text-align-right-tablet {
			text-align: right;
		}
		
	}

	@media (min-width: 1200px) {
		
		.rb-text-align-left-desktop {
			text-align: left;
		}
		
		.rb-text-align-center-desktop {
			text-align: center;
		}
		
		.rb-text-align-right-desktop {
			text-align: right;
		}
		
	}

   
/* =Images
   ========================================================================== */   
   
	.rb-image-circle { border-radius: 50%; }
   
/* =Visibility
   ========================================================================== */     
   
	@media (min-width: 768px) {
		
		.rb-mobile-only {
			display: none !important;
		}
		
	}
	
	@media (max-width: 767.98px) {
		
		.rb-hide-on-mobile {
			display: none;	
		}
		
	}
	
	@media (min-width: 768px) and (max-width: 1199.98px) {
		
		.rb-hide-on-tablet {
			display: none;	
		}
		
	}
	
	@media (min-width: 1200px) {
		
		.rb-hide-on-desktop {
			display: none;	
		}
		
	}
	
	.rb-header-sticky .rb-hide-on-sticky {
		display: none;
	}
	
	.rb-display-on-sticky {
		display: none;
	}
	
	.rb-header-sticky .rb-display-on-sticky {
		display: block;
	}
	
