/* =Author box
   ========================================================================== */ 

	.rb-author-box {
		border: 1px solid var(--border-color);
		padding: var(--spacing-3);
		text-align: center;
		border-radius: var(--border-radius-default) var(--border-radius-default) 0 0 ; 
	}	
	
	.rb-author-box .rb-author-avatar {
		margin-bottom: var(--spacing-3);
	}
	
	.rb-author-box .rb-author-avatar img {
		display: inline-block;
		width: 90px !important;
	}
	
	.rb-author-box .rb-author-details {}
	
	.rb-author-box .rb-author-details h4 a {
		text-decoration: none;
	}
	
	.rb-author-box .rb-author-details > *:last-child {
		margin-bottom: 0;
	}
	
	.rb-author-box + .rb-author-box {
		border-top: 0;
		border-radius: 0;
	}
	
	.rb-author-box + .rb-author-box:last-child {
		border-radius: 0 0 var(--border-radius-default) var(--border-radius-default); 
		border-top: 0;
	}
	
	.rb-author-box:only-child {
		border-radius: var(--border-radius-default); 
	}
	
	.rb-author-box img {
		border-radius: 50%;
	}
		
	.rb-author-box ul {
		list-style: none;
		margin-bottom: 0;
	}
	
	.rb-author-box ul li {
		display: inline-block;
		padding: var(--font-spacing-1);
	}
	
	.rb-author-box ul li a {
		color: var(--color-text);
		text-decoration: none;
	}
	
	.rb-author-box ul li i {
		padding-right: var(--font-spacing-1);
	}
	
	@media( min-width: 768px ) {
		
		.rb-author-box {
			display: flex;
			align-items: center;
			padding: var(--spacing-4);
			text-align: left;
		}
		
		.rtl .rb-author-box {
			text-align: right;
		}
		
		.rb-author-box .rb-author-avatar {
			flex: 0 0 120px;
			margin-bottom: 0;
		}
		
		.rtl .rb-author-box .rb-author-avatar {
			text-align: right;
		}
		
		.rb-author-box .rb-author-details {
			flex: 1;
		}
		
	}
   
	@media( min-width: 1200px ) {
		
		.rb-author-box {
			padding: var(--spacing-5);
		}
		
	}
