/* ==========================================================================
   =Comments Form
   ========================================================================== */
   
	.comment-respond {
		background-color: var(--background-color);
		padding: var(--spacing-3);
		border-radius: var(--border-radius-default);
	}
   
	/*
	 * 1. Clearfix hack 
	 */
	 
	.comment-respond:after {	/* 1 */
		visibility: hidden;
		display: block;
		font-size: 0;
		content: " ";
		clear: both;
		height: 0;
	}
   
	.comment-reply-title { margin-bottom: 0; }
   
	.comment-form { margin: 0 calc(-1 * var(--font-spacing-3)); }
	
	/*
	 * 1. Clearfix hack 
	 */
	 
	.comment-form:after {	/* 1 */
		visibility: hidden;
		display: block;
		font-size: 0;
		content: " ";
		clear: both;
		height: 0;
	}
	
	.comment-form label {}
	
	.comment-form p:not(.comment-notes):not(.logged-in-as) {
		margin-bottom: 0;
	}
	
	.comment-form .logged-in-as,
	.comment-form .comment-notes,
	.comment-form .comment-form-comment,
	.comment-form .comment-form-author,
	.comment-form .comment-form-email,
	.comment-form .comment-form-url,
	.comment-form-cookies-consent,
	.comment-form .form-submit {
		box-sizing: border-box;
		padding-right: var(--font-spacing-3);
		padding-left: var(--font-spacing-3);
	}

	.comment-form .form-submit input[type="submit"] {
		margin-bottom: 0;
	}

	@media (min-width: 768px) {
		
		.comment-respond {
			padding: var(--spacing-4);
		}
		
		.comment-form-author,
		.comment-form-email,
		.comment-form-url {
			width: 33.33333333%;
			float: left;
		}
		
		.rtl .comment-form-author,
		.rtl .comment-form-email,
		.rtl .comment-form-url {
			float: right;
		}
		
	}	
