/* ==========================================================================
   =Media
   ========================================================================== */   
   
	/* Make sure embeds and iframes fit their containers. */
	embed,
	iframe,
	object,
	video {
		margin-bottom: 1.75em;
		max-width: 100%;
		vertical-align: middle;
	}
   
	/* Hide first embed for video - audio - gallery post types */
	.single-post.single-format-video .post > .rb-media-wrapper.rb-media-video:first-of-type,
	.single-post.single-format-audio .post > .rb-media-wrapper.rb-media-audio:first-of-type,
	.single-post.single-format-gallery .post > .rb-gallery-wrapper:first-of-type,
	.single-post.single-format-audio .post > .wp-audio-shortcode:first-of-type,
	.single-post.single-format-video .post > .is-type-video:first-of-type,
	.single-post.single-format-audio .post > .wp-block-embed.is-type-audio:first-of-type,
	.single-post.single-format-audio .post > .rb-media-iframe:first-of-type {
		display: none;
	}

   
/* =Captions
   ========================================================================== */
   
    .wp-caption {
		margin-bottom: var(--font-spacing-4);
		max-width: 100%;
	}

	.wp-caption img[class*="wp-image-"] {
		display: block;
		margin: 0 auto;
		border-radius: var(--border-radius-default);
	}

	.wp-caption .wp-caption-text {
		padding: var(--font-spacing-2) 0;
	}

/* =Galleries
   ========================================================================== */

	.gallery { 
		margin-bottom: var(--font-spacing-4); 	
	}
	
	.gallery a {
		text-decoration: none;
	} 
	
	/*
	 * 1. Clearfix
	 */
	
	.gallery:after {	/* 1 */
		visibility: hidden;
		display: block;
		font-size: 0;
		content: " ";
		clear: both;
		height: 0;
	}

	.gallery-item {
		float: left;
		width: 100%;
		box-sizing: border-box;
		padding: 1.79104477%;
		text-align: center;
		vertical-align: top;
	}

	.rtl .gallery-item {
		float: right;
	}

	.gallery-columns-2 .gallery-item { max-width: 50%; }
	.gallery-columns-3 .gallery-item { max-width: 33.33%; }
	.gallery-columns-4 .gallery-item { max-width: 25%; }
	.gallery-columns-5 .gallery-item { max-width: 20%; }
	.gallery-columns-6 .gallery-item { max-width: 16.66%; }
	.gallery-columns-7 .gallery-item { max-width: 14.28%; }
	.gallery-columns-8 .gallery-item { max-width: 12.5%; }
	.gallery-columns-9 .gallery-item { max-width: 11.11%; }

	.gallery-icon img { 
		margin: 0 auto; 
		border-radius: var(--border-radius-default);
	}

	.gallery-caption {
		display: block;
		padding: var(--font-spacing-2) 0;
	}

	.gallery-columns-6 .gallery-caption,
	.gallery-columns-7 .gallery-caption,
	.gallery-columns-8 .gallery-caption,
	.gallery-columns-9 .gallery-caption {
		display: none;
	} 
	