/* ==========================================================================
   =Widgets: Default WordPress Widgets
   ========================================================================== */
	
	.rb-sidebar-notice {
		background-color: var(--background-color);
		border: 1px solid var(--border-color);
		padding: var(--spacing-2);
		border-radius: var(--border-radius-default);
		font-size: var(--font-size-1);
	}	
	
	.rb-sidebar-notice > * {
		margin: 0;
	}	
	
	.widget { 
		margin-bottom: var(--font-spacing-3); 
	}

	
	@media (min-width: 992px) {
		
		.widget:last-child { 
			margin-bottom: 0 !important; 
		}
		
	}
	
	/**
	 * 1. The last element of every widget shouldn't have a margin bottom. used for spacing purposes
	 */
	
	.widget > *:last-child { 
		margin-bottom: 0; /* 1 */
	}
	
	.widget > h5:first-child,
	.widget-title { 
		margin: 0 0 var(--font-spacing-3);
	}	
	
	.widget:after {	/* 1 */
		visibility: hidden;
		display: block;
		font-size: 0;
		content: " ";
		clear: both;
		height: 0;
	}
	
	@media(min-width: 768px) {
		
		.widget { 
			margin-bottom: var(--font-spacing-4); 
		}
		
		.widget > h5:first-child,
		.widget-title { 
			margin: 0 0 var(--font-spacing-4);
		}
		
	}
	
	.widget-title { 
		position: relative;
	}
	
	.widget-title-style-1:after {
		display: inline-block;
		content: "";
		width: var(--spacing-2);
		height: var(--spacing-2);
		margin-left: var(--spacing-2);
		background-color: var(--color-accent-2);
		border-radius: 50%;
	}

	.rtl .widget-title-style-1:after {
		margin-left: 0;
		margin-right: var(--spacing-2);
	}

	.widget-title-style-2:before {
		display: inline-block;
		content: "";
		width: var(--spacing-4);
		height: 3px;
		margin-right: var(--spacing-2);
		background-color: var(--color-accent-2);
	}

	.rtl .widget-title-style-2:before {
		margin-right: 0;
		margin-left: var(--spacing-2);
	}

	.widget-title-style-3 {
		color: #fff;
		background-color: var(--color-accent-2);
		border-radius: var(--border-radius-default);
		padding: var(--spacing-1) var(--spacing-3);
	}

	.widget-title-style-4:after {
		display: block;
		content: "";
		width: var(--spacing-5);
		height: 3px;
		margin-top: var(--spacing-2);
		margin-right: var(--spacing-2);
		background-color: var(--color-accent-2);
	}

	.rtl .widget-title-style-4:after {
		margin-right: 0;
		margin-left: var(--spacing-2);
	}

	.widget-title-style-5 {
		border-top: 1px solid var(--border-color);
		padding-top: var(--font-spacing-4);
	}

	.widget-title-style-6 {
		
	}

	.widget-title-style-6 svg {
		display: block;
	}

	.widget-title-style-6 svg path {
		stroke: var(--color-accent-2);
		fill: var(--color-accent-2);
	}


	.widget-title-style-7 span {
		position: relative;
		z-index: 1;
		display: inline-block;
		background-color: var(--background-color-body);
		padding-right: var(--spacing-3);
	}

	#rb-header-top .widget-title-style-7 span {
		background-color: var(--background-color-header-top);
	}	

	#rb-footer-top .widget-title-style-7 span {
		background-color: var(--background-color-footer-top);
	}	
	
	#rb-footer .widget-title-style-7 span {
		background-color: var(--background-color-footer);
	}	
	
	#rb-footer-bottom .widget-title-style-7 span {
		background-color: var(--background-color-footer-bottom);
	}	
	
	#rb-mobile-menu-wrap .widget-title-style-7 span {
		background-color: var(--background-color-mobile-menu-wrap);
	}	

	.rtl .widget-title-style-7 span {
		padding-right: 0;
		padding-left: var(--spacing-3);
	}

	.widget-title-style-7:after {
		content: "";
		position: absolute;
		right: 0;
		bottom: 50%;
		transform: translateY(-50%);
		background: var(--border-color);
		width: 100%;
		height: 1px;
		left: 0;
	}

	.widget-title-style-8 span {
		color: #fff;
		background-color: var(--color-accent-2);
		border-radius: var(--border-radius-default);
		padding: var(--spacing-1) var(--spacing-2);
	}

	.widget-title-style-9 {
		position: relative;
		padding-left: var(--spacing-3);
	}

	.widget-title-style-9:before {
		display: inline-block;
		content: "";
		width: 3px;
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		background-color: var(--color-accent-2);
	}

	.rtl .widget-title-style-9 {
		padding-right: 0;
		padding-right: var(--spacing-3);
	}

	.rtl .widget-title-style-9:before {
		left: auto;
		right: 0;
	}

	.widget-title-style-10 {
		border-bottom: 1px solid var(--border-color);
	}

	.widget-title-style-10 span {
		display: inline-block;
		border-bottom: 2px solid var(--color-accent-2);
		padding-bottom: var(--font-spacing-3);
		margin-bottom: -1px;
	}
	
/* =Archive Widget
   ========================================================================== */
	
	.widget_archive { color: var(--color-muted); }
	
	.widget_archive ul { list-style: none; }
	
	.widget_archive li { border-bottom: 1px solid var(--border-color); } 
	
	.widget_archive li:last-child { border-bottom: 0; }
	
	.widget_archive li a {
		display: inline-block;
		padding: var(--font-spacing-2) 0;
		text-decoration: none;
	}
		
	.widget_archive select { margin-bottom: 0; }
	
	.widget_archive option {}	
	
/* =Audio Widget
   ========================================================================== */	
	
	.widget_media_audio {}
		
/* =Calendar Widget
   ========================================================================== */
	
	.widget_calendar {}
	
	#calendar_wrap {}
		
	#calendar_wrap table {
		position: relative;
		border-collapse: separate;
		border-spacing: var(--font-spacing-1);
	}		
		
	#calendar_wrap table,
	#calendar_wrap th,
	#calendar_wrap td {
		border: 0;
		padding: 0;
		text-align: center;
		
	}
	
	#wp-calendar caption {
		margin-bottom: 0;
		text-align: center;
		background-color: inherit;
		border: 1px solid var(--border-color);
		border-radius: var(--border-radius-default);
		color: var(--color-text);
		line-height: 2;
	}
	
	#wp-calendar th {
		color: var(--color-text);
		line-height: 2;
	}	
	
	#wp-calendar tbody td:not(.pad) {
		border: 1px solid var(--border-color);
		border-radius: var(--border-radius-default);
		line-height: 1.5;
	}
	
	#wp-calendar  tbody td a {
		display: block;
		margin: 0;
		background-color: var(--color-accent-1);
		color: #fff;
		line-height: 1.5;
		text-align: center;
	}
	
	#wp-calendar  tbody td a:hover {
		color: var(--color-link-hover);
	}
	
	#wp-calendar #today {
		background-color: var(--color-accent-1);
		color: #ffffff;
	}
	
	#wp-calendar tfoot {
		height: 0;
		float: left;
	}
	
	#wp-calendar #prev a {
		position: absolute;
		top: 0;
		left: var(--font-spacing-1);
		display: inline-block;
		width: var(--font-spacing-6);
		line-height: var(--font-spacing-4);
		color: var(--color-link);
	}
	
	#wp-calendar #prev a:before {
		position: relative;
		display: inline-block;
		color: var(--color-link);
		content: "\e917";
		font-family: 'rovenblogiconfont';
		text-align: center;
	}
	
	#wp-calendar #prev a:hover:before {
		color: var(--color-link-hover);
	}
	
	#wp-calendar #next a {
		position: absolute;
		top: 0;
		right: var(--font-spacing-1);
		display: inline-block;
		width: var(--font-spacing-6);
		color: var(--color-link);
	}
	
	#wp-calendar #next a:after {
		position: relative;
		display: inline-block;
		color: var(--color-link);
		content: "\e918";
		font-family: 'rovenblogiconfont';
		text-align: center;
	}
	
	#wp-calendar #next a:hover:after {
		color: var(--color-link-hover);
	}
	
