/* =RSS Widget
   ========================================================================== */
	
	.widget_rss {}

	.widget_rss ul {
		margin: 0;
		list-style: none;
	}

	.widget_rss li { margin-bottom: var(--font-spacing-3); }

	.widget_rss ul:last-child,
	.widget_rss li:last-child { margin-bottom: 0; }

	.widget_rss ul li a.rsswidget {
		margin-top: var(--font-spacing-1); 
		margin-bottom: var(--font-spacing-4); 
	}

	.widget_rss .rsswidget img { margin-top: calc(-1 *var(--font-spacing-1)); }

	.widget_rss .rss-date,
	.widget_rss cite 	{
		display: block;
		padding: var(--font-spacing-3) 0;
	}
	
	.widget_rss cite {
		color: var(--color-headings);
	}

/* =Search Widget
   ========================================================================== */
	
	.widget_search {}
	
	.search-form {
		position: relative;
	}

	.search-field {}
	
	.widget_search .search-submit {
		position: absolute;
		top: 0;
		right: 0;
		border-radius: 0 var(--font-spacing-1) var(--font-spacing-1) 0;
		background-color: var(--color-accent-1);
		border-color: var(--color-accent-1);
		text-align: center;
	}
	
	.rtl .widget_search .search-submit {
		right: auto;
		left: 0;
		border-radius: var(--font-spacing-1) 0 0 var(--font-spacing-1);
	}
	
	.widget_search .search-submit:hover, 
	.widget_search .search-submit:focus {
		background-color: var(--color-accent-2);
		border-color: var(--color-accent-2);
		color: #ffffff;
	}
	
	.widget_search .search-submit:active {
		background-color: var(--color-accent-2);
		border-color: var(--color-accent-2);
		color: #ffffff;
	}

/* =Tag Cloud Widget
   ========================================================================== */
	
	.widget_tag_cloud {}
	
	.widget_tag_cloud a {
		display: inline-block;
		margin: 0 var(--font-spacing-1) var(--font-spacing-2) 0;
		padding: var(--font-spacing-1) var(--font-spacing-3);
		background-color: var(--color-accent-1);
		border-radius: var(--border-radius-buttons);
		color: #ffffff;
		transition: 0.5s all ease-in-out;
		font-size: var(--font-size-2);
		text-decoration: none;
	}
	
	.rtl .widget_tag_cloud a {
		margin: 0 0 var(--font-spacing-2) var(--font-spacing-1);
	}
	
	.widget_tag_cloud a:hover{
		background-color: var(--color-accent-2);
	}

