/* ==========================================================================
   =WordPress Alignments
   ========================================================================== */	

	.alignleft {
		float: left;
		display: inline;
		margin: var(--font-spacing-4) var(--font-spacing-4) var(--font-spacing-4) 0 !important;
	}

	.rtl .alignleft {
		float: right;
		margin: var(--font-spacing-4) 0 var(--font-spacing-4) var(--font-spacing-4) !important;
	}

	.alignright {
		float: right;
		display: inline;
		margin: var(--font-spacing-4) 0 var(--font-spacing-4) var(--font-spacing-4) !important;
	}
	
	.rtl .alignright {
		float: left;
		margin: var(--font-spacing-4) var(--font-spacing-4) var(--font-spacing-4) 0 !important;
	}

	.aligncenter {
		display: block;
		margin: var(--font-spacing-4) auto !important;
		clear: both;
	}
	
	img.alignleft,
	img.alignright,
	img.aligncenter {
		border-radius: var(--border-radius-default);
	}	