/* ==========================================================================
   =Masonry Grid
   ========================================================================== */
   
	.rb-masonry-grid {
		margin-bottom: var(--spacing-3);
	}	
	
	.rb-masonry-grid-item:not(:last-child) {
		padding-bottom: var(--spacing-3);
	}
	
	.rb-masonry-grid-item > *:last-child {
		margin-bottom: 0;
	}
	
	@media (min-width: 768px) {
		
		.rb-masonry-grid {
			margin-bottom: var(--spacing-4);
		}
		
		.rb-masonry-grid-item:not(:last-child) {
			padding-bottom: var(--spacing-4);
		}
		
		.rb-masonry-grid.cols-2,
		.rb-masonry-grid.cols-3,
		.rb-masonry-grid.cols-4 {
			margin: calc( -1 * var(--spacing-3) ) calc( -1 * var(--spacing-3) ) var(--spacing-3) ;
		}
		
		.rb-masonry-grid.cols-2 .rb-masonry-grid-item,
		.rb-masonry-grid.cols-3 .rb-masonry-grid-item,
		.rb-masonry-grid.cols-4 .rb-masonry-grid-item {
			padding: var(--spacing-3);
			box-sizing: border-box;
		}
		
	}
	
	.rb-masonry-grid .rb-card-1,
	.rb-masonry-grid .rb-card-2,
	.rb-masonry-grid .rb-card-3,
	.rb-masonry-grid .rb-card-6,
	.rb-masonry-grid .rb-card-7 {
		padding-top: 0 !important;
	}

	.rb-masonry-grid .rb-card-1.rb-card-no-thumbnail .rb-card-content,
	.rb-masonry-grid .rb-card-2.rb-card-no-thumbnail .rb-card-content,
	.rb-masonry-grid .rb-card-3.rb-card-no-thumbnail .rb-card-content,
	.rb-masonry-grid .rb-card-6.rb-card-no-thumbnail .rb-card-content,
	.rb-masonry-grid .rb-card-7.rb-card-no-thumbnail .rb-card-content {
		position: relative !important;
		top: auto !important;
		left: auto !important;
		bottom: auto !important;
		right: auto !important;
		padding-left: 0;
		padding-right: 0;
		width: 100%;
	}

	.rb-masonry-grid .rb-card-1 .rb-card-background,
	.rb-masonry-grid .rb-card-2 .rb-card-background,
	.rb-masonry-grid .rb-card-3 .rb-card-background,
	.rb-masonry-grid .rb-card-6 .rb-card-background,
	.rb-masonry-grid .rb-card-7 .rb-card-background {
		position: relative !important;
		top: auto !important;
		left: auto !important;
		bottom: auto !important;
		right: auto !important;
	}
		