/* =Contact Form Validator 
   ========================================================================== */     
   	
	#comment-error,
	#author-error,
	#email-error {
		color: var(--color-accent-2);
		font-size: var(--font-size-2);
		margin-top: calc( -1 * var(--font-spacing-3) );
		margin-bottom: var(--font-spacing-3);
	}
	
	#comment.error,
	#author.error,
	#email.error {
		border-color: var(--color-accent-2);
	}