/* ==========================================================================
   =Typography
   ========================================================================== */
 
	html {
		font-size: var(--font-base-size);
		scroll-behavior: smooth;
	}

	body {
		background-color: var(--background-color-body);
		color: var(--color-text);		
		font-family: var(--font-family-body);
		font-size: var(--font-size-body);
		font-weight: var(--font-weight-body);
		line-height: var(--line-height-body);
		letter-spacing: var(--letter-spacing-body);
	}
	
	body.rtl { 
	   direction: rtl; 
	   unicode-bidi: embed; 
	}

	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		color: var(--color-headings);
		font-family: var(--font-family-headings);
		font-weight: var(--font-weight-headings);
		line-height: var(--line-height-headings);
		letter-spacing: var(--letter-spacing-headings);
		margin: var(--font-spacing-4) 0;
	}
	
	h1 {
		font-size: var(--font-size-h1);
	}
	
	h2 {
		font-size: var(--font-size-h2);
	}
	
	h3 {
		font-size: var(--font-size-h3);
	}
	
	h4 {
		font-size: var(--font-size-h4);
	}
	
	h5 {
		font-size: var(--font-size-h5);
	}
	
	h6 {
		font-size: var(--font-size-h6);
	}
	
	h1:first-child,
	h2:first-child,
	h3:first-child,
	h4:first-child,
	h5:first-child,
	h6:first-child { 
		margin-top: 0 !important; 
	}
	
	p {	
		margin-bottom: var(--font-spacing-4); 
	}
	
	strong { 
		font-weight: bolder; 
	}
	
	em { 
		font-style: italic; 
	}
	
	small { 
		font-size: var(--font-size-2); 
	}
	
	big { 
		font-size: var(--font-size-4); 
	}
	
	sub { 
		font-size: var(--font-size-1);
		vertical-align: sub; 
	}
	
	sup { 
		font-size: var(--font-size-1);
		vertical-align: super; 
	}
	
	abbr[title] {  
		border-bottom: 1px dotted var(--color-text); 
		cursor: help;
	}
	
	address { 
		display: block; 
		margin-bottom: var(--font-spacing-4); 
	}
	
	blockquote {
		overflow: hidden;
	}
	
	code, pre, kbd {
		
	}
	
	code {

	}
	
	pre {
		display: block;
		margin-bottom: var(--font-spacing-4); 
		overflow-x: auto; 
		white-space: pre-wrap;
	}
	
	kbd {
		
	}
	
	mark,
	ins { text-decoration: none; }
	
	s,
	del { text-decoration: line-through; }
	
/* ==========================================================================
   =Forms
   ========================================================================== */
	

	form {}
	
	fieldset { display: block; }

	fieldset legend {}
	
	label { 
		display: block; 
		color: var(--color-form-labels);
	}
	
	input[type="text"],
	input[type="password"],
	input[type="date"],
	input[type="datetime"],
	input[type="datetime-local"],
	input[type="month"],
	input[type="week"],
	input[type="email"],
	input[type="number"],
	input[type="search"],
	input[type="tel"],
	input[type="time"],
	input[type="url"],
	input[type="color"],
	textarea {
		display: block;
		box-sizing: border-box;
		width: 100%;	
		font: inherit;
		padding: var(--font-spacing-2) var(--font-spacing-3);
		border-radius: var(--border-radius-inputs);
		border: 1px solid var(--border-color-form-elements);
		margin-bottom: var(--font-spacing-4);
		color: var(--color-form-elements);
		appearance: none;
		background-color: var(--background-color-form-elements);
	}
	
	input[type="text"]:focus,
	input[type="password"]:focus,
	input[type="date"]:focus,
	input[type="datetime"]:focus,
	input[type="datetime-local"]:focus,
	input[type="month"]:focus,
	input[type="week"]:focus,
	input[type="email"]:focus,
	input[type="number"]:focus,
	input[type="search"]:focus,
	input[type="tel"]:focus,
	input[type="time"]:focus,
	input[type="url"]:focus,
	input[type="color"]:focus,
	textarea:focus {
		border-color: var(--border-color-form-elements-focus);
		outline: none;
	}

	input[type="text"]:disabled,
	input[type="password"]:disabled,
	input[type="date"]:disabled,
	input[type="datetime"]:disabled,
	input[type="datetime-local"]:disabled,
	input[type="month"]:disabled,
	input[type="week"]:disabled,
	input[type="email"]:disabled,
	input[type="number"]:disabled,
	input[type="search"]:disabled,
	input[type="tel"]:disabled,
	input[type="time"]:disabled,
	input[type="url"]:disabled,
	input[type="color"]:disabled,
	textarea:disabled {
		cursor: not-allowed;
		background-color: var(--background-color-form-elements-disabled);
	}

	input[type="text"][disabled],
	input[type="text"][readonly],
	fieldset[disabled] input[type="text"],
	input[type="password"][disabled],
	input[type="password"][readonly],
	fieldset[disabled] input[type="password"],
	input[type="date"][disabled],
	input[type="date"][readonly],
	fieldset[disabled] input[type="date"],
	input[type="datetime"][disabled],
	input[type="datetime"][readonly],
	fieldset[disabled] input[type="datetime"],
	input[type="datetime-local"][disabled],
	input[type="datetime-local"][readonly],
	fieldset[disabled] input[type="datetime-local"],
	input[type="month"][disabled],
	input[type="month"][readonly],
	fieldset[disabled] input[type="month"],
	input[type="week"][disabled],
	input[type="week"][readonly],
	fieldset[disabled] input[type="week"],
	input[type="email"][disabled],
	input[type="email"][readonly],
	fieldset[disabled] input[type="email"],
	input[type="number"][disabled],
	input[type="number"][readonly],
	fieldset[disabled] input[type="number"],
	input[type="search"][disabled],
	input[type="search"][readonly],
	fieldset[disabled] input[type="search"],
	input[type="tel"][disabled],
	input[type="tel"][readonly],
	fieldset[disabled] input[type="tel"],
	input[type="time"][disabled],
	input[type="time"][readonly],
	fieldset[disabled] input[type="time"],
	input[type="url"][disabled],
	input[type="url"][readonly],
	fieldset[disabled] input[type="url"],
	input[type="color"][disabled],
	input[type="color"][readonly],
	fieldset[disabled] input[type="color"],
	textarea[disabled],
	textarea[readonly],
	fieldset[disabled] textarea {
		cursor: not-allowed;
		background-color: var(--background-color-form-elements-disabled);
	}

	textarea[rows] { height: auto; }

	/**
	 *  1. Disallow resize out of parent
	 */
	
	textarea { max-width: 100%; /* 1 */ }
	
	select {
		box-sizing: border-box;
		width: 100%;
		font: inherit;
		padding: var(--font-spacing-2) var(--font-spacing-3);
		border-radius: var(--border-radius-inputs);
		border: 1px solid var(--border-color-form-elements);
		margin-bottom: var(--font-spacing-4);
		color: var(--color-form-elements);
		background-color: var(--background-color-form-elements);
	}

	select[multiple] { 
		height: auto;
		padding: var(--font-spacing-2) var(--font-spacing-3);
		line-height: var(--font-spacing-4);	
	}
	
	select:disabled { cursor: not-allowed; }
	
	select:focus { border-color: var(--border-color-form-elements-focus); }

	input[type="range"] { width: 50%; }
	
	input[type="file"]{
		height: auto;
		width: 100%;
		margin-bottom: var(--font-spacing-4);
	}
	
	input[type="checkbox"],
	input[type="radio"] {
		margin-bottom: 0;
	}

	input[type="checkbox"] + label,
	input[type="radio"] + label {
		display: inline-block;
		margin-right: var(--font-spacing-3);
		margin-bottom: var(--font-spacing-3);
		vertical-align: baseline;
	}
	
	.rtl input[type="checkbox"] + .rtl label,
	.rtl input[type="radio"] + .rtl label {
		margin-right: 0;
		margin-left: var(--font-spacing-3);
	}
	
	input[type="reset"],
	input[type="submit"],
	input[type="button"]{
		position: relative;
		display: inline-block;
		font: inherit;
		padding: var(--font-spacing-2) var(--font-spacing-4);		
		border: 1px solid var(--color-accent-1);
		border-radius: var(--border-radius-buttons);
		margin-bottom: var(--font-spacing-4);
		background-color: var(--color-accent-1);
		color: #ffffff;
		text-align: center;
		text-decoration: none;
		cursor: pointer;
		appearance: none;
	} 
	
	input[type="reset"]:hover,
	input[type="submit"]:hover,
	input[type="button"]:hover,
	input[type="reset"]:focus,
	input[type="submit"]:focus,
	input[type="button"]:focus {
		border: 1px solid var(--color-accent-2);
		background-color: var(--color-accent-2);
		color: #ffffff;
	}
	
	input[type="reset"]:active,
	input[type="submit"]:active,
	input[type="button"]:active {
		border-color: var(--color-accent-2);
		background-color: var(--color-accent-2);
		color: #ffffff;
	}		

	::placeholder { color: var(--color-form-placeholders); } 
		
/* ==========================================================================
   =Formatting
   ========================================================================== */

	hr {
		height: 1px;
		border: 0;
		margin: var(--font-spacing-4) 0;
		background-color: var(--border-color);
	}
	
/* ==========================================================================
   =Lists
   ========================================================================== */
   	
	ul, ol {
		margin-bottom: var(--font-spacing-4);
		list-style-position: inside;
	}

	ul { list-style-type: disc; }
	ol { list-style-type: decimal; }
	
	li {  }
	
	li > ul, 
	li > ol  {
		margin-bottom: 0;
		margin-left: var(--font-spacing-4);
	}
	
	.rtl li > ul, 
	.rtl li > ol  {
		margin-left: 0;
		margin-right: var(--font-spacing-4);
	}
	
	dl { margin-bottom: var(--font-spacing-4); }

	dt { }

	dd { margin-bottom: var(--font-spacing-4); } 
	
/* ==========================================================================
   =Images
   ========================================================================== */

	img { 
		max-width: 100%;
		height: auto;
		border: none; 
		vertical-align: middle;
	}
	
/* ==========================================================================
   =Tables
   ========================================================================== */

	table,
	th,
	td {  }

	table {
		width: 100%;
		border: 1px solid var(--border-color);
		border-collapse: collapse;
		border-spacing: 0;
		border-radius: var(--border-radius-default);
		margin-bottom: var(--font-spacing-4);
		table-layout: auto;
	}

	caption {
		margin-bottom: var(--font-spacing-4);
		text-align: left;
	}

	th {
		padding: var(--font-spacing-3); 
		border: 1px solid var(--border-color);
		text-align: left;
	}

	td { 
		padding: var(--font-spacing-3); 
		border: 1px solid var(--border-color);
		text-align: left;
	}

/* ==========================================================================
   =Links
   ========================================================================== */

	a {
		color: var(--color-link);
		transition: color 0.25s;
	}

	a:focus { color: var(--color-link); }
	
	a:hover { color: var(--color-link-hover); }
	
	a:active { color: var(--color-link-active); }	
	