/* ==========================================================================
   =Widgets: Custom Widgets
   ========================================================================== */
   	
	
/* =RovenBlog: Text
   ========================================================================== */
   
	.rb-widget-text {}
   
/* =RovenBlog: Archives
   ========================================================================== */
   
	.rb-widget-archive {}
	
	.rb-widget-archive ul { 
		list-style: none; 
	}
	
	.rb-widget-archive li { 
		border-bottom: 1px solid var(--border-color); 
		padding: var(--spacing-3) 0;
		display: flex;
		justify-content: space-between;
		align-items: center;
	} 
	
	.rb-widget-archive li:last-child { 
		border-bottom: 0; 
	}
	
	.rb-widget-archive li a {
		display: inline-block;	
		text-decoration: none;
	}
	
	.rb-widget-archive li span {
		width: 35px;
		height: 35px;
		line-height: 35px;
		background-color: var(--color-accent-2);
		color: #ffffff;
		text-align: center;
		border-radius: 50%;
		font-size: var(--font-size-1);
	}   
   
/* =RovenBlog: Categories
   ========================================================================== */

	.rb-widget-categories {}
	
	.rb-widget-categories ul { 
		list-style: none; 
	}
	
	.rb-widget-categories li { 
		border-bottom: 1px solid var(--border-color); 
		padding: var(--spacing-3) 0;
		display: flex;
		justify-content: space-between;
		align-items: center;
	} 
	
	.rb-widget-categories li:last-child { 
		border-bottom: 0; 
	}
	
	.rb-widget-categories li a {
		display: inline-block;	
		text-decoration: none;
	}
	
	.rb-widget-categories li span {
		width: 35px;
		height: 35px;
		line-height: 35px;
		background-color: var(--color-accent-2);
		color: #ffffff;
		text-align: center;
		border-radius: 50%;
		font-size: var(--font-size-1);
	}

/* =RovenBlog: Categories with background image
   ========================================================================== */

	.rb-widget-categories-bg-image { }
	
	.rb-widget-categories-bg-image ul { 
		list-style: none;
	}
	
	.rb-widget-categories-bg-image ul li {
		margin-bottom: var(--spacing-3);
		border-radius: var(--border-radius-default);
		overflow: hidden;
	} 
	
	.rb-widget-categories-bg-image ul li a {
		display: block;
		background-position: center center;
		background-repeat: no-repeat;
		background-size: cover;
		text-decoration: none;
	}
	
	.rb-widget-categories-bg-image-content {
		padding: var(--spacing-3);
		display: flex;
		align-items: center;
		justify-content: space-between;
		background-color: rgba(0, 0, 0, 0.4);
		
	}
	
	.rb-widget-categories-bg-image-title {
		margin: 0 var(--spacing-3) 0 0;		
		color: #ffffff;
		display: inline;
		flex-basis: 40px;
		flex-grow: 1;
	}
	
	.rb-widget-categories-bg-image-count {
		background-color: var(--color-accent-2);
		color: #ffffff;
		width: 40px;
		height: 40px;
		line-height: 40px;
		text-align: center;
		border-radius: 50%;
		font-size: 80%;
	}
	
	@media (min-width: 768px) {
		
		.rb-widget-categories-bg-image-content {
			padding: var(--spacing-3) var(--spacing-4);
		}
		
	}

/* =RovenBlog: Tag Cloud
   ========================================================================== */

	.rb-widget-tag-cloud {}
		
	.rb-widget-tag-cloud a {
		display: inline-block;
		margin: 0 var(--font-spacing-1) var(--font-spacing-2) 0;
		padding: var(--font-spacing-1) var(--font-spacing-3);
		background-color: var(--color-accent-1);
		border-radius: var(--border-radius-buttons);
		color: #ffffff;
		transition: 0.5s all ease-in-out;
		font-size: var(--font-size-2);
		text-decoration: none;
	}

	.rtl .rb-widget-tag-cloud a {
		margin: 0 0 var(--font-spacing-2) var(--font-spacing-1);
	}

	.rb-widget-tag-cloud a:hover{
		background-color: var(--color-accent-2);
	}

/* =RovenBlog: Social Networks
   ========================================================================== */

	.rb-widget-social-networks {}

	.rb-widget-social-networks ul {
		list-style: none;
	}
	
	.rb-widget-social-networks ul li {
		display: inline-block;
	}
	
	.rb-widget-social-networks ul li a {
		display: inline-block;
		color: #ffffff;
		width: 40px;
		height: 40px;
		line-height: 40px;
		border-radius: 50%;
		text-align: center;
		margin: 1px 1px 2px 0;
		text-decoration: none;
	}
	
	.rb-widget-social-networks ul li a i {
		line-height: 40px;
		font-size: 100%;
	}

/* =RovenBlog: Social Networks List
   ========================================================================== */

	.rb-widget-social-networks-list {}

	.rb-widget-social-networks-list ul {
		list-style: none;
	}
	
	.rb-widget-social-networks-list ul li {
		padding: var(--spacing-2) var(--spacing-3);
		margin-bottom: var(--spacing-2);
		border-radius: var(--border-radius-default);
		font-size: 90%;
		transition: 0.3s ease-in-out;
	}
	
	.rb-widget-social-networks-list ul li:hover {
		padding-left: var(--spacing-4);
	}
	
	.rtl .rb-widget-social-networks-list ul li:hover {
		padding-right: var(--spacing-4);
		padding-left: 0;
	}

	.rb-widget-social-networks-list ul li:last-child {
		margin-bottom: 0;
	}
	
	.rb-widget-social-networks-list ul li a {
		color: #ffffff;
		display: flex;
		align-items: center;
		text-decoration: none;
	}
	
	.rb-widget-social-networks-list ul li a i {
		line-height: 1;
		margin-right: var(--spacing-3);
		font-size: 130%;
	}
	
	.rtl .rb-widget-social-networks-list ul li i {
		margin-right: 0;
		margin-left: var(--spacing-3);
	}

	.rb-widget-social-networks-list ul li a span {
		font-size: 120%;
	}

/* =RovenBlog: Social Networks List Alternative
   ========================================================================== */

	.rb-widget-social-networks-list-alt {}

	.rb-widget-social-networks-list-alt ul {
		list-style: none;
	}
	
	.rb-widget-social-networks-list-alt ul li {
		padding: var(--spacing-1) var(--spacing-2) var(--spacing-1) var(--spacing-1);
		display: inline-block;
	}
	
	.rtl .rb-widget-social-networks-list-alt ul li {
		padding: var(--spacing-1) var(--spacing-1) var(--spacing-1) var(--spacing-2);
	}
	
	.rb-widget-social-networks-list-alt ul.rb-widget-social-networks-list-alt-hide-name li {
		padding: 0 0 var(--spacing-1) var(--spacing-1);
	}

	.rb-widget-social-networks-list-alt ul li a {
		display: flex;
		align-items: center;
		text-decoration: none;
	}
	
	.rb-widget-social-networks-list-alt ul:not(.rb-widget-social-networks-list-alt-same-color) li a {
		color: currentcolor;
	}
	
	.rb-widget-social-networks-list-alt ul:not(.rb-widget-social-networks-list-alt-same-color) li a span {
		color: var(--color-link);
	}
	
	.rb-widget-social-networks-list-alt-hide-name span {
		display: none;
	}
	
	.rb-widget-social-networks-list-alt ul li a i {
		line-height: 1;
		margin-right: var(--spacing-2);
		font-size: 130%;
		display: inline-block;
	}
	
	.rtl .rb-widget-social-networks-list-alt ul li a i {
		margin-right: 0;
		margin-left: var(--spacing-2);
	}
	
	.rb-widget-social-networks-list-alt ul.rb-widget-social-networks-list-alt-hide-name li a i  {
		margin-right: 0;
	}
	
/* =RovenBlog: Posts
   ========================================================================== */   
  
	.rb-widget-posts {}		
	
	.rb-widget-posts ul {
		list-style: none;
	}
	
	.rb-widget-posts ul li {
		margin-bottom: var(--spacing-3);
		display: flex;
		flex-direction: row;
		flex-wrap: nowrap;
		align-items: center;
	}
	
	.rb-widget-posts ul li:last-child {
		margin-bottom: 0;
	}
	
	.rb-widget-posts ul.rb-widget-posts-boxed li {
		border: 1px solid var(--border-color);
		padding: var(--spacing-3);
		border-radius: var(--border-radius-default);
		margin-bottom: var(--spacing-3);
	} 
	
	.rb-widget-posts ul.rb-widget-posts-boxed li:last-child {
		margin-bottom: 0;
	}
	
	.rb-widget-posts ul.rb-widget-posts-line li {
		border-bottom: 1px solid var(--border-color);
		padding-bottom: var(--spacing-3);
		margin-bottom: var(--spacing-3);
	}
	
	.rb-widget-posts ul.rb-widget-posts-line li:last-child {
		border-bottom: 0;
		margin-bottom: 0;
	}
	
	.rb-widget-posts ul li .rb-widget-posts-thumbnail-wrap {
		width: 75px;
		overflow: hidden;
		border-radius: var(--border-radius-default);
	}
	
	.rb-widget-posts a {
		text-decoration: none;
	}
	
	.rb-widget-posts ul li .rb-widget-posts-thumbnail-wrap a {
		
	}
	
	.rb-widget-posts ul li .rb-widget-posts-thumbnail-wrap a img {
		width: 100%;
		object-fit: cover;
		transition: 0.6s ease;		
	}
	
	.rb-widget-posts ul li .rb-widget-posts-thumbnail-wrap a img:hover {
		transform: scale(1.1);
	}
	
	.rb-widget-posts ul li .rb-widget-posts-content-wrap {
		flex-grow: 1;
		flex-basis: 75px;
		padding-bottom: var(--spacing-3); 
	}
	
	.rb-widget-posts-boxed .rb-widget-posts-content-wrap {
		padding-bottom: 0 !important;
	}
	
	.rb-widget-posts-thumbnail-wrap + .rb-widget-posts-content-wrap {
		padding-left: var(--spacing-3) !important; 
		padding-bottom: 0 !important;
	}
	
	.rtl .rb-widget-posts-thumbnail-wrap + .rb-widget-posts-content-wrap {
		padding-left:0 !important; 
		padding-right: var(--spacing-3) !important; 
	}
	
	.rb-widget-posts ul li .rb-widget-posts-content-wrap .rb-widget-posts-title {
		margin-bottom: var(--spacing-1);
		margin-top: 0;
	}
	
	.rb-widget-posts ul li .rb-widget-posts-content-wrap .rb-widget-posts-title a {
		display: inline;
		position: relative;
	}
	
	.rb-widget-posts ul li .rb-widget-posts-content-wrap .rb-widget-posts-meta {}
		
	.rb-widget-posts ul li .rb-widget-posts-content-wrap .rb-widget-posts-meta-category a {
		display: inline-block;
		font-size: 75%;
		padding: 8px 10px 6px;
		line-height: 0.5;
		border-radius: var(--border-radius-default);
		background-color: var(--color-accent-2);
		color: #fff;
	}	
		
	.rb-widget-posts ul li .rb-widget-posts-content-wrap .rb-widget-posts-meta .rb-widget-posts-meta-date {
		display: block;
		font-size: var(--font-size-2);
	}
	
	.rb-widget-posts ul li .rb-widget-posts-content-wrap .rb-widget-posts-meta .rb-widget-posts-meta-author {
		display: block;
		font-size: var(--font-size-2);
	}
	
	.rb-widget-posts ul li .rb-widget-posts-content-wrap .rb-widget-posts-meta .rb-widget-posts-meta-author img {
		border-radius: 50%;
	}
	
	@media (min-width: 768px) {
		
		.rb-widget-posts ul li .rb-widget-posts-thumbnail-wrap {
			width: 100px;
		}
		
		.rb-widget-posts ul li .rb-widget-posts-content-wrap {
			flex-basis: 100px;
		}
		
		.rb-widget-posts ul li .rb-widget-posts-content-wrap .rb-widget-posts-meta .rb-widget-posts-meta-date {
			display: inline-block;
		}
		
		.rb-widget-posts ul li .rb-widget-posts-content-wrap .rb-widget-posts-meta .rb-widget-posts-meta-author {
			display: inline-block;
		}
		
		.rb-widget-posts-meta-date + .rb-widget-posts-meta-author:before {
			content: " • ";
			padding: 0 var(--spacing-1); 
			color: var(--color-accent-2);	
		}
			
	}
  
/* =RovenBlog: About Author
   ========================================================================== */   
    
	.rb-widget-about-author {
		
	}	
	
	.rb-widget-about-author > div {
		padding: var(--spacing-4) var(--spacing-3);
		border-radius: var(--border-radius-default);
	}
	
	.rb-widget-about-author-background {
		background-color: var(--background-color);
	}
	
	.rb-widget-about-author-bordered {
		border: 1px solid var(--border-color);
	}
	
	.rb-widget-about-author-banner {
		margin: calc(-1 * var(--spacing-4)) calc(-1 * var(--spacing-3)) 0 calc(-1 * var(--spacing-3));
	}
	
	.rb-widget-about-author-banner img {
		border-radius: var(--border-radius-default) var(--border-radius-default) 0 0;
	}
	
	.rb-widget-about-author-banner + .rb-widget-about-author-avatar {
		margin-top: -65px;
	}
	
	.rb-widget-about-author-avatar img {
		border-radius: 50%;
		width: 125px;
	}
	
	.rb-widget-about-author-name {}
	
	.rb-widget-about-author-description {}
	
	.rb-widget-about-author-description > *:last-child {
		margin-bottom: 0;
	}
	
	@media (min-width: 992px) {
		
		.rb-widget-about-author > div {
			padding: var(--spacing-4);
		}
		
		.rb-widget-about-author-banner {
			margin: calc(-1 * var(--spacing-4)) calc(-1 * var(--spacing-4)) 0 calc(-1 * var(--spacing-4));
		}
		
	}
	
/* =RovenBlog: logo
   ========================================================================== */   
    	
	.rb-widget-logo {}	
	
	.rb-widget-logo a {
		display: block;
		text-decoration: none;
	}	
	
	.rb-widget-logo img.rb-widget-logo-dark-mode { display: none; }
	
	.rb-dark-mode .rb-widget-logo img.rb-widget-logo-dark-mode { display: inline-block; }
	
	.rb-dark-mode .rb-widget-logo img.rb-widget-logo-light-mode { display: none; }
	
/* =RovenBlog: Horizontal Menu
   ========================================================================== */   
    
	.rb-widget-horizontal-menu {}
	
	.rb-widget-horizontal-menu ul {
		list-style: none;
		margin-bottom: 0;
	}
	
	.rb-widget-horizontal-menu ul ul { display: none; }
	
	.rb-widget-horizontal-menu li {
		display: inline-block;
		padding: var(--spacing-1) 0;
	}
	
	.rb-widget-horizontal-menu li:not(:last-child):after {
		content: " • ";
		padding: 0 var(--spacing-1); 
		color: var(--color-accent-2);	
	}
	
	.rb-widget-horizontal-menu li a {
		text-decoration: none;
	}
	
	.rb-widget-horizontal-menu ul.menu li a {
		background-image: linear-gradient(90deg, currentColor, currentColor);
		background-repeat: no-repeat;
		background-size: 0% 2px;
		transition: 0.6s cubic-bezier(0.25, 0.8, 0.25, 1), background-position 0s;
		display: inline;
		padding: 2px 0;
	}

	.rb-widget-horizontal-menu ul.menu li a:hover {
		background-size: 100% 2px;
	}

	.rb-widget-horizontal-menu ul.menu li a {
		background-position: right 0% bottom;
	}

	.rb-widget-horizontal-menu ul.menu li a:hover {
		background-position: left 0% bottom;
	}
	
	@media (min-width: 768px) {
		
		.rb-widget-horizontal-menu li:not(:last-child):after {
			padding: 0 var(--spacing-2); 
		}
		
	}