/* ==========================================================================
   =Comments, Pingbacks and Trackbacks
   ========================================================================== */
   
   /*
	 * 1. Clearfix hack 
	 */
	
	.comments-area:after,
	.comment-list:after	
	.comment:after {	/* 1 */
		visibility: hidden;
		display: block;
		font-size: 0;
		content: " ";
		clear: both;
		height: 0;
	}
	
	.comments-area {
		margin-bottom: var(--font-spacing-4);
	}
	
	.comment-list,
	.comment-list .children {
		list-style: none;
	}
	
	.comment-list {
		margin: 0 0 var(--spacing-4);
	}
	
	@media (max-width: 480px) {
		
		.comment-list .children {
			margin-left: var(--font-spacing-2);
		}
		
		.rtl .comment-list .children {
			margin-left: 0;
			margin-right: var(--font-spacing-2);
		}
		
	}

	.comment {}
	
	.no-comments, 
	.comment-awaiting-moderation {
		color: var(--color-headings);
	}
	
/* =Comment Meta
   ========================================================================== */	
   
	.comment-meta {
		margin: var(--font-spacing-3) 0;
	}
	
	.comment-author {
		position: relative;
		z-index: 2;
	}
	
	.comment-author img {
		border-radius: var(--border-radius-default); 
	}
	
	.comment-author .avatar {
		position: absolute;
		left: calc(-1 * var(--font-spacing-7));
		top: calc(-1 * var(--font-spacing-3));
		height: var(--font-spacing-6);
		width: var(--font-spacing-6);
	}
	
	.rtl .comment-author .avatar {
		left: auto;
		right: calc(-1 * var(--font-spacing-7));
	}
	
	@media (max-width: 480px) {
		
		.comment-author .avatar {
			position: relative;
			left: auto;
			top: auto;
			display: block;
			height: var(--font-spacing-5);
			width: var(--font-spacing-5);
			margin-bottom: var(--font-spacing-2);
		}
		
		.rtl .comment-author .avatar {
			right: auto;
		}
		
	}
	
	.comment-author .says {
		display: none;
	}
	
	.comment-metadata >  a {
		color: var(--color-text-general);
		text-decoration: none;
	}
	
	.comment-metadata a.comment-edit-link {}
	
/* =Comment Body
   ========================================================================== */	
   
	.comment .comment-body {
		position: relative;
		padding: var(--font-spacing-4) 0 var(--font-spacing-4) var(--font-spacing-7);
		border-top: 1px solid var(--border-color);	
	}
	
	.rtl .comment .comment-body {
		padding: var(--font-spacing-4) var(--font-spacing-7) var(--font-spacing-4) 0;
	}
	
	.comment-list > li:first-child > .comment-body {
		padding-top: var(--font-spacing-2);
		border-top: 0;
	}
	
	@media (max-width: 480px) {
		
		.comment .comment-body {
			padding: var(--font-spacing-3) 0 var(--font-spacing-3) 0;
		}
		
	}
	
	.comment .comment-body  *:last-child {
		margin-bottom: 0;
	}	
	
/* =Comment Reply
   ========================================================================== */	
   
	.reply {
		margin: var(--font-spacing-3) 0 var(--font-spacing-4);
	}
	
	.comment-reply-link {
		position: relative;
	}
  
/* ==========================================================================
   =Comments Navigation
   ========================================================================== */

	.comment-navigation { 
		padding-top: var(--font-spacing-4);
		margin-bottom: var(--font-spacing-4); 
	}
	
	/*
	 * 1. Clearfix hack 
	 */
	 
	.comment-navigation:after {	/* 1 */
		visibility: hidden;
		display: block;
		font-size: 0;
		content: " ";
		clear: both;
		height: 0;
	}
	
	#comment-nav-above {
		padding-bottom: var(--font-spacing-4);
		border-bottom: 1px solid var(--border-color);
	}
	
	#comment-nav-below {
		border-top: 1px solid var(--border-color);
	}

	.comment-navigation a {
		text-decoration: none;
	}

	.comment-navigation .nav-next a:after {
		font-family: 'rovenblogiconfont';
		content: "\e918";
		padding-left: var(--font-spacing-1);
	}

	.rtl .comment-navigation .nav-next a:after {
		content: none;
	}	

	.comment-navigation .nav-previous a:before {
		font-family: 'rovenblogiconfont';
		content: "\e917";
		padding-right: var(--font-spacing-1);
	}		
	
	.rtl .comment-navigation .nav-previous a:before {
		content: none;
	}
	
	.comment-navigation .nav-next{
		float: right;
		text-align: right;
		width: 50%;
	}
	
	.rtl .comment-navigation .nav-next {
		float: left;
		text-align: left;
	}	
	
	.comment-navigation .nav-previous {
		float: left;
		width: 50%;
	}
   
	.rtl .comment-navigation .nav-previous {
		float: right;	
	}