/* ==========================================================================
   =Post(s)
   ========================================================================== */
   
	.post {	
		position: relative; 
	}
	
	.post > *:last-child {
		margin-bottom: 0;
	}
	

/* =Sticky Post + Other Required Classes
   ========================================================================== */
   
	.sticky { 
		/* Required class WordPress */
	}
	
	.updated:not(.published) {
		display: none;
	}
	
	.bypostauthor {
		/* Required WordPress Class */
	}
	
/* =Page Links
   ========================================================================== */
   
	.page-links {
		margin: 0 0 var(--spacing-4) 0;
		clear: both;
	}
   
	.page-links .page-number {
	}
   
	.page-links a {
		color: var(--color-link);
		display: inline-block;
		text-decoration: none;
	}
	
	.page-links a:hover {
		color: var(--color-link-hover);
	}
	
	.page-links a:active {
		color: var(--color-link-active);
	}
	
	.page-links a span {
		padding: var(--spacing-1) var(--spacing-3);
		display: inline-block;
	}
	
	.page-links .current span {
		color: var(--color-text);
		background-color: var(--background-color);
		border-radius: var(--border-radius-buttons);
		padding: var(--spacing-1) var(--spacing-3);
		display: inline-block;
	}
	
/* =Tags 
   ========================================================================== */

	.tags-links {
		text-align: center;
		clear: both;
	}
	
	.rb-has-sidebar .tags-links {
		text-align: left;
	}
	
	.rtl.rb-has-sidebar .tags-links {
		text-align: right;
	}
	
	.tags-links a,
	.tags-links a:visited {
		position: relative;
		display: inline-block;
		padding: var(--spacing-1) var(--spacing-3);
		margin: var(--spacing-1) var(--spacing-2) var(--spacing-1) 0;
		border-radius: var(--border-radius-buttons);
		background-color: var(--color-accent-1);
		color: #ffffff;
		text-decoration: none;
	}
	
	.rtl .tags-links a,
	.rtl .tags-links a:visited {
		margin: var(--spacing-1) 0 var(--spacing-1) var(--spacing-2);
	}
	
	.tags-links a:hover {
		color: #ffffff;
		background-color: var(--color-accent-2);
	}
	